import './Register.css';
import React, { useEffect } from 'react';
import PopupWithForm from '../PopupWithForm/PopupWithForm';
import useValidationForForm from '../../hooks/useForm';

const Register = ({
  onClose,
  isOpen,
  onRedirect,
  onClick,
  onRegister,
  messageOnRegister,
  isSubmitting,
}) => {
  const {
    values,
    handleChange,
    errors,
    isValid,
    resetForm,
  } = useValidationForForm();

  const regPassword =
    '^(?=.*[A-Za-z])(?=.*\\d)(?=.*[@$!%*#?&~])[A-Za-z\\d@$!%*#?&~]{8,}$';

  useEffect(() => {
    resetForm();
  }, [isOpen, resetForm]);

  const handleRegisterSubmit = (e) => {
    e.preventDefault();

    onRegister(values.email, values.password, values.name);
  };

  return (
    <PopupWithForm
      name="regstration"
      title="–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è"
      savebtn="–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"
      onClose={onClose}
      onClick={onClick}
      isOpen={isOpen}
      isValid={isValid}
      redirectTo="–í–æ–π—Ç–∏"
      onRedirect={onRedirect}
      onSubmit={handleRegisterSubmit}
      errorMassageOnSubmit={messageOnRegister}
      isSubmitting={isSubmitting}
    >
      <fieldset className="popup__input-container">
        <p className="popup__input-title">Email</p>
        <input
          type="email"
          name="email"
          placeholder="–í–≤–µ–¥–∏—Ç–µ email"
          className="popup__input"
          minLength="5"
          maxLength="40"
          autoComplete="off"
          value={values.email || ''}
          onChange={handleChange}
          required
          disabled={isSubmitting}
        />
        <span className="popup__error">{errors.email || ''}</span>
      </fieldset>

      <fieldset className="popup__input-container">
        <p className="popup__input-title">–ü–∞—Ä–æ–ª—å</p>
        <input
          type="password"
          name="password"
          placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          className="popup__input"
          minLength="8"
          maxLength="30"
          autoComplete="off"
          pattern={regPassword}
          value={values.password || ''}
          onChange={handleChange}
          required
          disabled={isSubmitting}
        />
        <span className="popup__error">
          {errors.password
            ? '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –≤–∫–ª—é—á–∞—Ç—å –±—É–∫–≤—ã, —Ü—ã—Ñ—Ä—ã, —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª—ã (@$!%*#?&~), –Ω–µ –º–µ–Ω–µ–µ 8 –∑–Ω–∞–∫–æ–≤'
            : ''}
        </span>
      </fieldset>

      <fieldset className="popup__input-container">
        <p className="popup__input-title">–ò–º—è</p>
        <input
          type="text"
          name="name"
          placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ—ë –∏–º—è"
          className="popup__input"
          minLength="2"
          maxLength="30"
          autoComplete="off"
          value={values.name || ''}
          onChange={handleChange}
          required
          disabled={isSubmitting}
        />
        <span className="popup__error">{errors.name || ''}</span>
      </fieldset>
    </PopupWithForm>
  );
};

export default Register;
