import React, { useEffect } from 'react';
import './Login.css';
import PopupWithForm from '../PopupWithForm/PopupWithForm';
import useValidationForForm from '../../hooks/useForm';

const Login = ({
  onClose,
  isOpen,
  onRedirect,
  onClick,
  onLogin,
  messageOnLogin,
  isSubmitting,
}) => {
  const {
    values,
    handleChange,
    errors,
    isValid,
    resetForm,
  } = useValidationForForm();

  useEffect(() => {
    resetForm();
  }, [isOpen, resetForm]);

  const handleLoginSubmit = (e) => {
    e.preventDefault();

    onLogin(values.email, values.password);
  };

  return (
    <PopupWithForm
      name="login"
      title="–í—Ö–æ–¥"
      savebtn="–í–æ–π—Ç–∏"
      isOpen={isOpen}
      onClose={onClose}
      onRedirect={onRedirect}
      onClick={onClick}
      isValid={isValid}
      redirectTo="–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"
      onSubmit={handleLoginSubmit}
      errorMassageOnSubmit={messageOnLogin}
      isSubmitting={isSubmitting}
    >
      <fieldset className="popup__input-container">
        <p className="popup__input-title">Email</p>
        <input
          type="email"
          name="email"
          placeholder="–í–≤–µ–¥–∏—Ç–µ email"
          className="popup__input"
          minLength="5"
          maxLength="40"
          autoComplete="off"
          value={values.email || ''}
          onChange={handleChange}
          required
          disabled={isSubmitting}
        />
        <span className="popup__error">{errors.email || ''}</span>
      </fieldset>

      <fieldset className="popup__input-container">
        <p className="popup__input-title">–ü–∞—Ä–æ–ª—å</p>
        <input
          type="password"
          name="password"
          placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          className="popup__input"
          minLength="8"
          maxLength="30"
          autoComplete="off"
          value={values.password || ''}
          onChange={handleChange}
          required
          disabled={isSubmitting}
        />
        <span className="popup__error">
          {errors.password
            ? '–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –≤–∫–ª—é—á–∞—Ç—å –±—É–∫–≤—ã, —Ü—ã—Ñ—Ä—ã, —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª—ã (@$!%*#?&~), –Ω–µ –º–µ–Ω–µ–µ 8 –∑–Ω–∞–∫–æ–≤'
            : ''}
        </span>
      </fieldset>
    </PopupWithForm>
  );
};

export default Login;
