import './SearchForm.css';
import React, { useState } from 'react';

function SearchForm({ onSearch }) {
  const [placeholderMessage, setPlaceholderMessage] = useState(
    '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–º—É –Ω–æ–≤–æ—Å—Ç–∏',
  );

  const [inputValue, setInputValue] = useState('');
  const [isSearchFormValid, setIsSearchFormValid] = React.useState(false);

  const handleChangeSearchForm = (event) => {
    const { target } = event;
    const { value } = target;

    setInputValue(value);
    setIsSearchFormValid(target.closest('input').checkValidity());
  };

  const handleNewsSearch = (e) => {
    e.preventDefault();

    if (isSearchFormValid) {
      onSearch(inputValue);
    } else {
      setInputValue('');
      setPlaceholderMessage('–ù—É–∂–Ω–æ –≤–≤–µ—Å—Ç–∏ –∫–ª—é—á–µ–≤–æ–µ —Å–ª–æ–≤–æ');
      localStorage.removeItem('query');
    }
  };

  React.useEffect(() => {
    if ('query' in localStorage) {
      setInputValue(localStorage.getItem('query'));
    }
  }, []);

  return (
    <div className="search-form">
      <h1 className="search-form__title">–ß—Ç–æ —Ç–≤–æ—Ä–∏—Ç—Å—è –≤ –º–∏—Ä–µ?</h1>
      <p className="search-form__text">
        –ù–∞—Ö–æ–¥–∏—Ç–µ —Å–∞–º—ã–µ —Å–≤–µ–∂–∏–µ —Å—Ç–∞—Ç—å–∏ –Ω–∞ –ª—é–±—É—é —Ç–µ–º—É –∏ —Å–æ—Ö—Ä–∞–Ω—è–π—Ç–µ –≤ —Å–≤–æ—ë–º –ª–∏—á–Ω–æ–º
        –∫–∞–±–∏–Ω–µ—Ç–µ.
      </p>
      <div className="search-form__input-fild">
        <input
          type="text"
          name="search"
          className="search-form__input"
          minLength="2"
          maxLength="40"
          autoComplete="off"
          pattern="^[A-Za-z–ê-–Ø–∞-—è]{2,40}$"
          value={inputValue}
          placeholder={placeholderMessage}
          onChange={handleChangeSearchForm}
          required
        />

        <button
          type="button"
          aria-label="–ò—Å–∫–∞—Ç—å"
          className="search-form__search-button"
          onClick={handleNewsSearch}
        >
          –ò—Å–∫–∞—Ç—å
        </button>
      </div>
    </div>
  );
}

export default SearchForm;
